<script lang="ts">
import { Options, Vue } from "vue-class-component";
import InfoTableCard from "@/components/InfoTableCard.vue";
import PowerCard from "@/components/PowerCard.vue";
// import { initConfig, updateDimensions } from "@/assets/js/themeConfig";

// import { initGraph, } from "@/components/model";
import { msgInit, msgSubscribe, msgStop } from "@/common/processMessages";
// import PgSelector from "@/components/PgSelector.vue";
import { ycValues } from "@/common/model";



@Options({
	components: {
		// PgSelector,
		InfoTableCard,
		PowerCard,
	},
})

export default class App extends Vue {

	pin = ''
	pinConfirm = ''
	autoLockInterval?: ReturnType<typeof setTimeout>;
	showPass = false;
	codeConfirmed = false;
	stepChangePin = 0;
	get isUpdateInProgress() {
		return ycValues.isUpdateInProgress;
	}
	get isBootDone() {
		return ycValues.isBootDone;
	}

	reload() {
		location.reload();
	}

	showModal = false;
	// methods
	init() {
		this.resetOrInitLockInterval();
		setTimeout(() => {

			if (ycValues.pinCode == "") {
				this.showModal = true;
			}
		}, 3000);

	}

	nextStepChangePin() {
		switch (this.stepChangePin) {
			default:
			case 0:
				console.log(this.pin, ycValues.pinCode)
				if (this.pin != ycValues.pinCode) {
					this.pin = ''
					return;
				}
				this.pin = ''
				this.stepChangePin = 1;
				break;
			case 1:
				this.pinConfirm = this.pin;
				this.pin = ''
				this.stepChangePin = 2;
				break;
			case 2:
				if (this.pin != this.pinConfirm) {
					this.pin = ''
					return;
				}
				this.savePin();
				this.stepChangePin = 0;
				break;
		}
	}

	closeChangePin() {
		this.stepChangePin = 0;
		this.showModal = false
		this.pin = "";
		this.pinConfirm = "";
		this.codeConfirmed = false;
	}

	resetOrInitLockInterval() {
		if (this.autoLockInterval) {
			clearInterval(this.autoLockInterval)
		}
		this.autoLockInterval = setInterval(() => {
			if (ycValues.isLocked || this.showModal) {
				return;
			}
			this.showPass = false
			this.pin = ''
			ycValues.isLocked = true
		}, 1000 * ycValues.displayTimeout)
	}

	savePin() {
		if (this.pin.length < 4) {
			return;
		}

		ycValues.isLocked = false
		ycValues.pinCode = this.pin;
		this.resetOrInitLockInterval();
		this.closeChangePin();

	}

	toggleShowPass() {
		this.showPass = !this.showPass;
	}


	removeFromPin() {
		this.pin = this.pin.slice(0, -1)
	}
	addToPin(value: number) {
		this.pin += value
	}
	unlock() {
		if (this.pin == ycValues.pinCode) {
			ycValues.isLocked = false
			this.pin = ''
			this.showModal = false
			this.resetOrInitLockInterval();
		} else {
			this.pin = ''
		}
	}

	get homeclass() {
		return 'home container-fluid px-2 m-0' + (ycValues.isLocked ? ' locked' : '')
	}

	get phaseAmount() {
		return ycValues.phasesInUse;
	}
	get locked() {
		return ycValues.isLocked;
	}

	setLocked() {
		this.pin = ''
		this.showPass = false
		ycValues.isLocked = !ycValues.isLocked;
	}
	changePin() {
		this.stepChangePin = 0;
		this.pin = ''
		this.pinConfirm = ''
		this.showPass = false
		this.showModal = true;
	}

	mounted() {
		this.init()
		// window.addEventListener('resize', updateDimensions)
		window.addEventListener('visibilitychange', this.visibilityChange)
		// window.addEventListener('blur',this.lostFocus)
		msgInit()
	}

	visibilityChange() {
		if (document.visibilityState !== "visible" || document.hidden) {
			this.lostFocus()
		} else {
			this.haveFocus()
		}
	}
	haveFocus() {
		msgSubscribe();
	}
	lostFocus() {
		msgStop();
	}
}
</script>

