import { reactive } from 'vue'
import { updateServerCpLock, updateSeverPin } from './processMessages';

export let graphData: {
	[key: number]: number
} = reactive({})

export function setGraphData(data: {
	[key: number]: number
}) {
	const oneDayAgo = Date.now() - 1000 * 60 * 60 * 24;
	for (const key of Object.keys(data)) {
		const numberKey = Number(key)
		if (numberKey >= oneDayAgo) {
			graphData[numberKey] = data[numberKey];
		} else {
			delete graphData[numberKey];
		}
	}
	graphData = data;
}

export const ycValues:YCValues = reactive<YCValues>({
	isPlugged: false,
	isCharging: false,
	isBootDone: false,
	isUpdateInProgress: false,
	imported: 0,
	evseCurrent: 0,
	displayTimeout: 30,
	currents: [0, 0, 0],
	voltages: [0, 0, 0],
	serialNumber: "",
	meterModel: "",
	__pinCode: "",
	socketStatus: {
		imported_wh: 0,
		exported_wh: 0,
		current: 0,
		voltage: 0,
		power: 0,
		power_factor: 0,
		serial: "",
		model: "",
		last_update: "",
	},
	__isLocked: false,
	isEnabled: false,
	metered: 0,
	chargePower: 0,
	minAmpsConfigured: 0,
	maxAmpsConfigured: 0,
	energySincePlugged: 0,
	energyChargedToday: 0,
	energyLimit: 0,
	isSocketActive: false,
	isSocketInstalled: false,
	phasesInUse: 0,
	get pinCode() {
		return this.__pinCode;
	},
	set pinCode(value: string) {
		this.__pinCode = value;
		updateSeverPin(value);
	},
	maxPower: () => ycValues.maxAmpsConfigured * (ycValues.voltages.reduce((acc, x) => x + acc, 0) / 3) * 3,
	get isLocked() {
		return this.__isLocked
	},
	set isLocked(locked: boolean) {
		this.__isLocked = locked
		updateServerCpLock(locked)
	},
	version: "",
} );


export interface YCValues {
	__pinCode: string
	__isLocked: boolean
	phasesInUse: number
	isPlugged: boolean
	isCharging: boolean
	imported: number
	evseCurrent: number
	currents: Array<number>
	voltages: Array<number>
	serialNumber: string
	meterModel: string
	pinCode: string
	isLocked: boolean
	isEnabled: boolean
	metered: number
	chargePower: number
	minAmpsConfigured: number
	maxAmpsConfigured: number
	energySincePlugged: number
	energyChargedToday: number
	energyLimit: number
	displayTimeout: number
	isSocketActive: boolean
	isSocketInstalled: boolean
	isBootDone: boolean
	isUpdateInProgress: boolean
	version: string
	socketStatus: {
		imported_wh: number,
		exported_wh: number,
		current: number,
		voltage: number,
		power: number,
		power_factor: number,
		serial: string,
		model: string,
		last_update: string
	}
	lastRfid?: {
		rfid: string,
		timestamp: Date
	}
	maxPower: () => number
}