import { mqttPublish, mqttRegister, mqttSubscribe, mqttUnsubscribe } from './client'
import { graphData, setGraphData, ycValues } from './model'
import { mqttClientId } from './client'

const ycMatches: { [key: string]: (message: string) => void } = {
	"openWB/internal_chargepoint/0/get/plug_state": extractPlugStatus,
	"openWB/internal_chargepoint/0/get/charge_state": extractChargeStatus,
	"openWB/internal_chargepoint/0/get/imported": extractMeteredValueWh,
	"openWB/internal_chargepoint/0/set/manual_lock": extractLockedValue,
	"openWB/internal_chargepoint/0/get/power": extractChargePowerValue,
	"openWB/internal_chargepoint/0/get/evse_current": extractProvidedCurrentValue,
	"openWB/internal_chargepoint/0/get/currents": extractChargeCurrentJsonValues,
	"openWB/internal_chargepoint/0/get/voltages": extractVoltageJsonValues,
	"openWB/internal_chargepoint/0/get/serial_number": extractLpSerial,
	"openWB/internal_chargepoint/0/get/meter_model": extractLpMeterModel,
	"openWB/internal_chargepoint/0/get/phases_in_use": extractPhaseInUse,
	"openWB/system/version": extractVersion,
	"openWB/system/boot_done": extractBootDone,
	"openWB/system/update_in_progress": extractUpdateInProgress,
	"openWB/optional/int_display/standby": extractStandBy,
	"openWB/optional/int_display/pin_code": extractPinCode,
	"yourCharge/config/min_evse_current_allowed": extractMinAmpsConfigured,
	"yourCharge/config/max_evse_current_allowed": extractMaxAmpsConfigured,
	"yourCharge/config/standard_socket_installed": extractStandardSocketInstalled,
	"yourCharge/status/standard_socket": extractSocketStatus,
	"yourCharge/status/cp_enabled": extractCpEnabled,
	"yourCharge/status/scanned_rfid": extractLastRfid,
	"yourCharge/control/socket_activated": extractSocketActivated,
	"yourCharge/status/energy_charged_since_plugged": extractEnergyChargedSincePluggedValue,
	"yourCharge/status/energy_charged_today": extractEnergyChargedTodayValueWh,
	"yourCharge/status/energy_limit": extractEnergyLimitSincePlugged,
}
const topicsToSubscribe = [
	...Object.keys(ycMatches),
	'openWB/command/' + mqttClientId() + '/#',
]
export function msgInit() {
	mqttRegister(processMqttMessage);
	msgSubscribe();
}
export function msgSubscribe() {
	console.log('MQTT subscribing register')
	topicsToSubscribe.forEach((topic) => {
		mqttSubscribe(topic)
	})
}

export function msgStop() {
	console.log('MQTT subscribing stop')
	topicsToSubscribe.forEach((topic) => {
		mqttUnsubscribe(topic)
	})
}


export function updateServerCpLock(lock: boolean) {
	sendServerMessage("openWB/set/internal_chargepoint/0/set/manual_lock", lock)
}

export function updateSeverPin(pin: string) {
	sendServerMessage("openWB/set/optional/int_display/pin_code", pin)
}


function sendServerMessage(
	topic: string,
	value: string | number | boolean | object,
) {
	switch (typeof value) {
		case 'number':
			mqttPublish(topic, JSON.stringify(+value))
			break
		case 'object':
			mqttPublish(topic, JSON.stringify(value))
			break
		case 'string':
			mqttPublish(topic, JSON.stringify(value))
			break
		default:
			mqttPublish(topic, JSON.stringify(value))
	}
}
function processMqttMessage(topicVal: string, payload: Buffer) {
	const topic = topicVal
	const message = payload.toString()
	if (topic in ycMatches) {
		ycMatches[topic](message);
		// console.log('Processed message: ' + topic + ' ' + message)
	} else {
		console.log('Ignored message: ' + topic + ' ' + message)
	}
}

function extractPlugStatus(message: string) {
	ycValues.isPlugged = message == 'true';
}
function extractChargeStatus(message: string) {
	ycValues.isCharging = message == 'true';
}
function extractMeteredValueWh(message: string) {
	ycValues.metered = Number(message);
}
function extractChargePowerValue(message: string) {
	ycValues.chargePower = Number(message);
	// ((ycValues.chargePower + 100) 
	// ycValues.chargePower = Math.random() * 10000;
	const items = graphData;
	items[Date.now()] = ycValues.chargePower;
	setGraphData(items);
}
function extractLockedValue(message: string) {
	ycValues.__isLocked = message == "true";
}
function extractProvidedCurrentValue(message: string) {
	ycValues.evseCurrent = Number(message);
}
function extractChargeCurrentJsonValues(message: string) {
	ycValues.currents = JSON.parse(message);
}
function extractVoltageJsonValues(message: string) {
	ycValues.voltages = JSON.parse(message);
}
function extractLpSerial(message: string) {
	ycValues.serialNumber = message;
}
function extractLpMeterModel(message: string) {
	ycValues.meterModel = message;
}
function extractPhaseInUse(message: string) {
	ycValues.phasesInUse = +message;
}
function extractVersion(message: string) {
	ycValues.version = message;
}
function extractMinAmpsConfigured(message: string) {
	ycValues.minAmpsConfigured = Number(message);
}
function extractMaxAmpsConfigured(message: string) {
	ycValues.maxAmpsConfigured = Number(message);
}
function extractStandardSocketInstalled(message: string) {
	ycValues.isSocketInstalled = message == "true";
}
function extractSocketStatus(message: string) {
	ycValues.socketStatus = JSON.parse(message);
}
function extractCpEnabled(message: string) {
	ycValues.isEnabled = message == "true";
}
function extractLastRfid(message: string) {
	ycValues.lastRfid = JSON.parse(message);
}
function extractSocketActivated(message: string) {
	ycValues.isSocketActive = message == "true";
}
function extractBootDone(message: string) {
	ycValues.isBootDone = message == "true";
}
function extractUpdateInProgress(message: string) {
	ycValues.isUpdateInProgress = message == "true";
}
function extractEnergyChargedSincePluggedValue(message: string) {
	ycValues.energySincePlugged = Number(message);
}
function extractEnergyChargedTodayValueWh(message: string) {
	ycValues.energyChargedToday = Number(message);
}
function extractEnergyLimitSincePlugged(message: string) {
	ycValues.energyLimit = Number(message);
}
function extractStandBy(message: string) {
	ycValues.displayTimeout = Number(message);
}
function extractPinCode(message: string) {
	ycValues.__pinCode = JSON.parse(message);
}
