<template>
	<g :transform="'translate(' + margin.left + ',' + margin.top/2 + ')'">
		<g
			id="XAxis"
			class="axis"
			:origin="drawAxis1"
			:transform="'translate(0,' + (height - 6) + ')'"
		/>
	</g>
</template>

<script setup lang="ts">
import {  defineProps } from 'vue';
import type { AxisContainerElement, ScaleTime } from 'd3'
import { axisBottom, extent, scaleTime, select, timeFormat } from 'd3'
import { computed } from 'vue'
import { graphData } from '@/common/model';

const props = defineProps<{
	width: number
	height: number
	margin: { left: number; top: number; right: number; bottom: number }
}>()
// state
const fontsize = 14

// computed
const xAxisGenerator = computed(() => {
	return axisBottom<Date>(xScale.value as ScaleTime<number, number, never>)
		.ticks(4)
		.tickSizeInner(0)
		.tickFormat(timeFormat('%H:%M'))
})
const xScale = computed(() => {
	// let e = extent(graphData, (d) => d.date)
	let e = extent(Object.keys(graphData), (d) => Number(d))
	// let e = Object.keys(graphData).map(Number);
	if (e[0] && e[1]) {
		return scaleTime<number>().domain(e).range([0, props.width])
	} else {
		return scaleTime().range([0, 0])
	}
})

const drawAxis1 = computed(() => {
	let axis = select<AxisContainerElement, number>('g#XAxis')
	axis.selectAll('*').remove()
		axis.call(xAxisGenerator.value)

	axis
		.selectAll('.tick > text')
		.attr('fill', () =>
		"var(--color-axis)"
		)
		.attr('font-size', fontsize +"px")
		axis.selectAll('.tick line').attr('stroke', 'var(--color-axis)')
	axis.select('.domain').attr('stroke', 'var(--color-axis)')
	axis
		.append('text')
		.attr('x', -props.margin.left)
		.attr('y', 0)
		.attr('fill', 'var(--color-axis)')
		.attr('font-size', fontsize + "px")
		.text('kW')
		.attr('text-anchor', 'start')
	return 'XAxis.vue'
})
</script>

<style></style>
