<template>
	<svg :width="width + margin.left + margin.right" :height="height + margin.top + margin.bottom">
		<g id="PowerGraph" :origin="draw" :transform="'translate(' + margin.left + ',' + margin.top + ')'" />
	</svg>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { defineProps } from 'vue';
import type { Selection, BaseType } from 'd3'
import { select, scaleLinear, axisLeft, area } from 'd3'
import {ycValues, graphData } from '@/common/model';
import { watch } from 'vue';

const props = defineProps<{
	width: number
	height: number
	margin: { left: number; top: number; right: number; bottom: number }
	stackOrder: number

}>()

const draw = computed(() => {

	const graph = select('g#PowerGraph')
	drawGraph(graph)
	graph.selectAll('.axis').remove()
	const yAxis = graph.append('g').attr('class', 'axis')
	yAxis.call(yAxisGenerator.value)
	yAxis
		.selectAll('.tick')
		.attr('font-size', 12)
		.attr('color', 'var(--color-axis)')
	yAxis.selectAll('.tick line').attr('stroke', 'var(--color-axis)')

	yAxis.select('.domain').attr('stroke', 'var(--color-axis)')
	return 'PowerGraph.vue'
})

const iScale = computed(() => {
	const keys = Object.keys(graphData).map((e) => Number(e));
	return scaleLinear()
		.domain([Math.min(...keys), Math.max(...keys,ycValues.maxPower())])
		.range([0, props.width]);
});

const yScale = computed(() => {
	const highestValue = Math.max(...Object.values(graphData),ycValues.maxPower());
	return scaleLinear()
		.range([props.height - 10, 0])
		.domain(
			[0, Math.ceil(highestValue)],
		)
})



const yAxisGenerator = computed(() => {
	return axisLeft<number>(yScale.value)
		.tickSizeInner(-props.width)
		.ticks(4)
		.tickFormat((d: number) =>
			(d == 0 ? '' : (Math.round(d * 10) / 10)/1000).toLocaleString(undefined),
		)
})
function drawGraph(graph: Selection<BaseType, unknown, HTMLElement, never>) {
	const data = Object.entries(graphData).map(([key, value]) => ({
		x: Number(key),
		y: value
	}));

	const area1 = area<{ x: number; y: number }>()
		.x((d) => iScale.value(d.x))
		.y0(yScale.value(0))
		.y1((d) => yScale.value(d.y));

	graph.selectAll('*').remove();
	graph
		.append('path')
		.datum(data)
		.attr('class', 'usagearea')
		.attr('d', area1)
		.attr('fill', 'var(--main-color)');
}

watch(() => graphData, () => {
	const graph = select('g#PowerGraph');
	drawGraph(graph);
}, { deep: true });
</script>

<style scoped>
svg {
	overflow: visible;
}

.usagearea {
	opacity: 0.7;
}
</style>
